import React, {Component} from "react";
import "../styling/style.css"
import SignUp from "../components/signup";
import Navbar from "../components/navbar"


class Login extends Component {
    render() {
        return (

            <div className="container">
                <Navbar/>


                <div className="hero">
                    <div className="section-50-images"style={{width:"50%",height:"50%"}}>
                        <img src="../static/undraw_studying_s3l7.svg"/>

                    </div>

                    <div className="section-50-column">
                        <div className="heading" style={{"color":"black"}}>Sign Up/Login</div>
                        <SignUp style={{alignSelf: "center"}}/>
                    </div>
                </div>


            </div>
        )
    }

}


export default Login;